@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,400;0,700;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #272626;
  background-color: var(--primary-dark);
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(1rem + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root{
  --primary-white:#ffffff;
  --primary-orange:#ffc107;
  --primary-dark:#272626;
  --primary-red: #d9534f;
  --primary-black: #000000;
  --primary-hover-red: #d9534f;
  --primary-v-line: #282c34;
  --primary-socialmedia: #4d4e4e;;

}
body{
  font-family: "Roboto Condensed" sans-serif;
}
/************************ Navbar ***********************************/
nav{
  z-index: 2;
  background: #272626;
  background: var(--primary-dark);
 
}

.logo{
  width:5rem;
  height:0.5;
  padding: 0.6rem;
  margin-left: 2rem; 
  
}

/************************Menu links***********************************/
nav a.nav-link{
  font-weight: 500;
  letter-spacing:0.0375rem;
  cursor:pointer;
}

nav ul li{
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem; 
  color: #ffffff !important; 
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid #272626;
  border-top: 0.1875rem solid var(--primary-dark);
}
.navbar-brand{
  max-width:17.25rem;
  padding:0 !important;
}

@media(max-width:768px)
{
  .navbar-brand{
    width: 7rem  !important;

  }}

.navbar-nav > li > a.active,  .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #ffc107;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #ffc107;
  border-top: 0.1875rem solid var(--primary-orange);
}

/************************ Header ***********************************/
.header-wraper{
  position:relative;
  background: url(/portfolio/static/media/headerimage2.3c30f283.jpg) no-repeat; 
  background-size: cover;
  background-position: center;
  height: 90vh;
}

.main-info{
  display:-webkit-flex;
  display:flex;
  position:absolute;
  top:0;
  left:0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: "Robot Condesed" , sans-serif;
  z-index: 1;

}
.main-info h1{
  color:#ffc107;
  color:var(--primary-orange);
  text-transform: uppercase;
}


/************************ Typed Text ***********************************/
.typed-text{
  font-size: 2rem;
  color:#ffffff;
  color:var(--primary-white);
}

@media(max-width:768px)
{
  .main-info h1{
    font-size: 1.5rem !important;

  }
  .typed-text{
    font-size: 1.3rem !important;
  }
}


